html,
body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  background: transparent;
  overflow: hidden;
  /* testing  */
  /* background: url(https://puu.sh/x4Qhi/dcce1de30b.jpg); */
  color: #fff;
  font-size: 12px;
}

h3 {
  display: none;
}
.inactive h3 {
  display: block;
}
.encounter {
  display: none;
}
.inactive .combatants {
  display: none;
}
.combatants {
  display: -ms-flexbox;
  display: flex;
  text-align: center;
}
.row {
  -ms-flex: 1 1 0%;
      flex: 1 1 0%;
  text-shadow: 0 0 8px #000;
  margin: 0 6px;
}
.row.self div.dps {
  background: rgba(255, 255, 255, 0.8);
  color: #000;
}
.row .dps {
  background: rgba(0, 0, 0, 0.3);
  color: #fff;
  -webkit-transform: skew(-30deg);
      -ms-transform: skew(-30deg);
          transform: skew(-30deg);
  text-shadow: initial;
  padding: 2px 0 3px;
}
.row > div {
  margin: 0px auto;
  padding: 0 0 1px;
}
.row div.dps > div {
  -webkit-transform: skew(30deg);
      -ms-transform: skew(30deg);
          transform: skew(30deg);
}
.row div.dps > div:after {
  clear: both;
  content: "";
  display: table;
}
.character-job {
  text-transform: uppercase;
  float: left;
  padding-left: 12px;
}
.damage-stats {
  float: right;
  padding-right: 12px;
}
.rank {
  display: none;
}



